* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Odibee Sans", cursive;
}

html {
  scroll-behavior: smooth;
  overflow: hidden;
}

body {
  background-image: url(./../img/bg.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

a {
  text-decoration: none;
}

.title-index__container {
  display: flex;
  justify-content: center;
}

.title-index {
  color: #009048;
  text-align: left;
  font-size: 80px;
  margin-top: 60px;
}

.title-o {
  color: #91ccaf;
}

.button-component__container {
  display: flex;
  justify-content: center;
}

.button-component {
  /* text-align: center; */
  width: 332px;
  height: 87px;
  font-size: 45px;
  background: #006cfc;
  color: white;
  border: 10px solid #001997;
  border-radius: 10px;
  margin-top: 50px;
}

.button-component:hover {
  background: #009efc;
  cursor: pointer;
}

.hands {
  height: 325px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.ready-sign {
  text-align: center;
  font-size: 45px;
  margin-top: 60px;
}

.img-hand__tijeras,
.img-hand__piedra,
.img-hand__papel {
  cursor: pointer;
}

.description-empezar {
  text-align: center;
  font-size: 40px;
  display: flex;
  justify-content: center;
  margin-top: 60px;
}

.cron-item {
  display: block;
  margin: auto;
  padding-top: 50px;
}

.deselected {
  opacity: 0.5;
  padding-top: 25px;
}

.selected {
  opacity: 1;
  padding-bottom: 25px;
}

.hands_result {
  display: grid;
  grid-template-columns: 100%;
  row-gap: 50px;
  padding: 20px 0 20px 0;
}

.hand-oponent {
  transform: rotate(180deg);
  width: 125px;
  margin: 0 auto;
}

.hand-selected {
  width: 125px;
  margin: 0 auto;
}

.result {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
}

.err-bg {
  background: rgba(137, 73, 73, 0.9);
}

.success-bg {
  background: rgba(136, 137, 73, 0.9);
}

.message-title {
  color: rgb(225, 225, 225);
  text-align: center;
  padding: 10px;
  margin-top: 60px;
}

.sign-result {
  display: block;
  margin: auto;
  padding-top: 25px;
}

.score-container {
  width: 250px;
  background: #fff;
  border: 10px solid #000000;
  border-radius: 10px;
  text-align: center;
  margin: 0 auto;
  padding: 15px;
}

.score-container h2 {
  font-size: 55px;
}

.score-container h3 {
  font-size: 45px;
  text-align: end;
}
